* {
  margin: 0;
  padding: 0;
  transition: 0.3s ease all;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 253, 111);
  width: 100%;
  height: 100vh;
}

.container {
  width: 80%;
  height: 90%;
  border-radius: 3px;
  background-color: white;
  overflow: hidden;
  box-shadow: 0px 0px 10px 1px grey;
  /* min-width: 700px; */
  position: relative;
}

@media only screen and (max-width: 900px) {
  .container {
    width: 90%;
  }
}

@media only screen and (max-width: 600px) {
  .container {
    width: 100%;
    height: 100%;
  }

  .App {
    align-items: flex-start;
    background-color: rgb(0, 255, 255);
  }
}

.header {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #81e2ff;
  padding: 0px 80px;
}

.header span {
  color: crimson;
}

.header-logo {
  font-family: "Times New Roman", Times, serif;
  font-size: 2rem;
  letter-spacing: 1px;
  font-weight: bold;
}

.header__options {
  display: flex;
}

a {
  text-decoration: none;
}

.header__option {
  font-weight: 500;
  margin-left: 100px;
  color: rgb(97, 97, 97);
  cursor: pointer;
}

#header__bar {
  position: absolute;
  right: 0px;
  bottom: 0px;
  height: 120px;
  width: 100%;
  color: white;
  background-color: rgb(245, 148, 21);
  transition: 0.5s ease all;
  z-index: 300;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.header__bar__title {
  font-size: 2rem;
  margin-bottom: 10px;
}

.header__bar__icons {
  color: white;
}

#header__bar img {
  height: 30px;
  width: 30px;
  margin: 0px 10px;
}

@media only screen and (max-width: 600px) {
  .header-logo {
    margin-right: -50px;
  }

  .header {
    padding: 0px 10px;
  }

  .header__option {
    font-size: 0.1rem;
  }
}

.home {
  display: flex;
  height: 100%;
}

@media only screen and (max-width: 600px) {
  .home {
    flex-direction: column;
  }
}

.sidebar {
  width: 280px;
  box-shadow: 5px 0px 10px 1px lightgrey;
  background-color: aliceblue;
}

.sidebar__option {
  padding: 20px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgb(238, 238, 238);
}

label {
  font-weight: 500;
}

select {
  border: none;
  font-size: 0.9rem;
  outline: none;
  background-color: transparent;
}

.MuiSlider-track {
  color: green;
}

.slider {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 1000px) {
  .sidebar {
    width: 200px;
  }

  .sidebar__option {
    padding: 10px;
  }

  select {
    font-size: 0.8rem;
  }

  label {
    font-size: 0.9rem;
  }
}

@media only screen and (max-width: 600px) {
  .sidebar {
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px 2px 10px 1px rgb(201, 191, 191);
  }

  .slider {
    max-width: 50px;
  }
  .sidebar__option {
    padding: 10px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2px;
    max-width: 72px;
    margin-right: -2px;
    border-bottom: none;
  }

  select {
    font-size: 0.8rem;
    font-weight: 500;
  }

  label {
    font-size: 0.9rem;
    display: none;
  }
}

.visuals {
  flex: 1 1;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

.visualizer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.legend {
  position: absolute;
  left: 0px;
  top: 0px;
  padding: 2px 5px;
  border: 1px solid grey;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  font-weight: 500;
}

.legend__lable {
  height: 5px;
  width: 20px;
  background-color: black;
  margin-right: 3px;
}

.visual__items {
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  height: 340px;
  margin-bottom: 40px;
  border-bottom: 1px solid black;
  position: relative;
}

.visual__item {
  position: absolute;
}

.visual__item > h4 {
  font-size: 0.4rem;
}

.visual__btns > button {
  width: 150px;
  background-color: rgb(0, 149, 199);
  padding: 7px 10px;
  border: none;
  border-radius: 5px;
  margin: 10px;
  font-size: 0.8rem;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
}

@media only screen and (max-width: 900px) {
  .visuals {
    padding: 5px;
  }
}

@media only screen and (max-width: 600px) {
  .visual__btns {
    display: flex;
    margin-top: -20px;
  }

  .visuals {
    padding-top: 10px;
  }

  .visual__btns > button {
    margin-bottom: -3px;
  }
}

